@use '@/theme/color.scss';
.projectList{
    padding: 10px;
    box-sizing: border-box;
    user-select: none;
    display: flex;
    flex-direction: column;
    gap: 10px;
    .label{
        font-weight: bold;
    }
    .projectItem{
        width: 100%;
        cursor: pointer;
        padding: 5px;
        border-radius: 4px;
        box-sizing: border-box;
        &:hover{
            background: color.$hoverBackgroundColor;
        }
    }
}